<template>
	<view class="container">
		<view class="search-bar">
			<view class="search-bar-wrap">
				<view class="search-input-wrap">
					<view class="search-icon">
						<text class="iconfont">üîç</text>
					</view>
				<input class="search-input" type="text" @confirm="handleSearch" placeholder="ÊêúÁ¥¢ÂÆ¢Êà∑ÂßìÂêç"
					v-model="searchKey" />
			</view>
			<view class="search-btn" @click="handleSearch">ÊêúÁ¥¢</view>
			<view class="filter-btn" @click="toggleFilterForm">
				<text>Á≠õÈÄâ</text>
				<text class="filter-icon">{{ showFilterForm ? '‚ñ≤' : '‚ñº' }}</text>
			</view>
			</view>

			<view class="filter-form" v-if="showFilterForm">
				<view class="form-row">
					<view class="form-group">
						<text class="form-label">ÊâãÊú∫Âè∑</text>
						<input class="form-select" type="text" v-model="phone" placeholder="ËØ∑ËæìÂÖ•ÊâãÊú∫Âè∑"
							style="height: 30px;font-size: 14px;" />
					</view>
					<view class="form-group">
						<text class="form-label">ÊâÄÂ±ûÈÉ®Èó®</text>
						<picker :range="departmentOptions" @change="departmentChange" class="form-select">
							<view class="picker-value">{{ departmentOptions[currentDepartment] }}</view>
						</picker>
					</view>
				</view>

				<view class="form-row">
					<view class="form-group">
						<text class="form-label">ÂÆ¢Êà∑Áä∂ÊÄÅ</text>
						<picker :range="statusOptions" @change="statusChange" class="form-select">
							<view class="picker-value">{{ statusOptions[currentStatus] }}</view>
						</picker>
					</view>
					<view class="form-group">
						<text class="form-label">ÂÆ°ÊâπÁä∂ÊÄÅ</text>
						<picker :range="approvalOptions" @change="approvalChange" class="form-select">
							<view class="picker-value">{{ approvalOptions[currentApproval] }}</view>
						</picker>
					</view>
				</view>
			</view>
		</view>

		<view class="main" :class="{ 'filter-shown': showFilterForm, 'filter-hidden': !showFilterForm }">
			<view class="customer-list">
				<view class="customer-item" v-for="(item, index) in customerList" :key="index">
					<view class="item-main">
						<view class="customer-info">
							<view class="name-status">
								<text class="name">{{ item.name || 'Êú™Áü•' }}</text>
								<text class="status" :class="'statusClass-' + (item.status || 1)">{{ dictMaps.customerStatus[item.status] || 'Êú™Áü•Áä∂ÊÄÅ' }}</text>
							</view>
							<view class="phone">
								<text class="label">ÊâãÊú∫Âè∑:</text>
								<text>{{ item.phone || 'Êú™Â°´ÂÜô' }}</text>
							</view>
							<view class="dept">
								<text class="label">ÊâÄÂ±ûÈÉ®Èó®:</text>
								<text>{{ item.branch_name || 'Êú™ÂàÜÈÖç' }}</text>
							</view>
							<view class="dept">
								<text class="label">ÂÆ¢Êà∑Á±ªÂûã:</text>
								<text>{{ dictMaps.customerType[item.client_type] || 'Êú™Áü•Á±ªÂûã' }}</text>
							</view>
							<view class="manager">
								<text class="label">‰∏öÂä°Âëò:</text>
								<text>{{ item.service_name || 'Êú™ÂàÜÈÖç' }}</text>
							</view>
							<view class="manager" v-if="item.product_name">
								<text class="label">‰∫ßÂìÅÂëò:</text>
								<text>{{ item.product_name }}</text>
							</view>
							<view class="create-time">
								<text class="label">ÂàõÂª∫Êó∂Èó¥:</text>
								<text>{{ formatDate(item.ctime) }}</text>
							</view>
						</view>
						<view class="approval-status">
							<text class="status-box" :class="'dealClass-' + (item.deal_status || 1)">
								{{ dictMaps.dealStatus[item.deal_status] || 'Êú™Áü•Áä∂ÊÄÅ' }}
							</text>
							<text class="client-age" v-if="item.age">{{ item.age }}Â≤Å</text>
							<text class="marital-status" v-if="item.matrimony">{{ dictMaps.maritalStatus[item.matrimony] }}</text>
						</view>
					</view>
					<view class="item-actions">
						<view class="action-btn" @click="goDetail(item)">
							<text class="iconfont icon-detail"></text>
							<text>ËØ¶ÊÉÖ</text>
						</view>
						<view class="action-btn" @click="goEdit(item)">
							<text class="iconfont icon-edit"></text>
							<text>ÁºñËæë</text>
						</view>
						<view class="action-btn" @click="goFollowup(item)">
							<text class="iconfont icon-comment"></text>
							<text>Ë∑üËøõËÆ∞ÂΩï</text>
						</view>
						<view class="action-btn" @click="updateStatus(item)">
							<text class="iconfont icon-status"></text>
							<text>Êõ¥Êñ∞Áä∂ÊÄÅ</text>
						</view>
						<view class="action-btn" @click="transferCustomer(item)">
							<text class="iconfont icon-status"></text>
							<text>Áßª‰∫§‰∫ßÂìÅÈÉ®</text>
						</view>
					</view>
				</view>

				<view class="empty-tip" v-if="customerList.length === 0 && !isLoading">
					ÊöÇÊó†ÂÆ¢Êà∑Êï∞ÊçÆ
				</view>
				
				<!-- Âä†ËΩΩÊõ¥Â§öÊèêÁ§∫ -->
				<view class="loading-more" v-if="customerList.length > 0">
					<text v-if="isLoading">Âä†ËΩΩ‰∏≠...</text>
					<text v-else-if="hasMore">‰∏äÊãâÂä†ËΩΩÊõ¥Â§ö</text>
					<text v-else>Ê≤°ÊúâÊõ¥Â§öÊï∞ÊçÆ‰∫Ü</text>
				</view>
			</view>
		</view>

		<view class="add-fab" @click="goAdd">
			<text style="transform: translateY(-2px);">+</text>
		</view>
	</view>
</template>

<script>
import customerApi from '@/api/customer.js';
import { dictMaps, getLabelByValue, customerType, customerStatus, dealStatus, customerProgress } from '@/utils/dict.js';
import { department } from '@/api/organization.js';
import tabbarUtils from '../../utils/tabbarUtils.js';

export default {
	data() {
		// Âü∫‰∫éÂ≠óÂÖ∏ÊûÑÂª∫ÈÄâÈ°πÊï∞ÁªÑ
		const buildOptions = (dictArray) => {
			return ['ÂÖ®ÈÉ®', ...dictArray.map(item => item.label)];
		};
		
		return {
			searchKey: '',
			phone: '',
			statusFilter: 'all',
			customerList: [],
			showFilterForm: false,
			departmentOptions: ['ÂÖ®ÈÉ®'], // ÂàùÂßãÂåñ‰∏∫ÂåÖÂê´"ÂÖ®ÈÉ®"ÈÄâÈ°πÁöÑÊï∞ÁªÑ
			departmentData: [], // Â≠òÂÇ®ÂÆåÊï¥ÁöÑÈÉ®Èó®Êï∞ÊçÆ
			currentDepartment: 0,
			statusOptions: buildOptions(customerStatus), // ‰ΩøÁî®customerStatusÂ≠óÂÖ∏ÊûÑÂª∫
			currentStatus: 0,
			approvalOptions: buildOptions(customerProgress), // ‰ΩøÁî®customerProgressÂ≠óÂÖ∏ÊûÑÂª∫
			currentApproval: 0,
			customerGroupOptions: ['ÂÖ®ÈÉ®', 'Ê∂àË¥π', 'ÁªèËê•', 'Ê∂àË¥πÁªèËê•'],
			currentCustomerGroup: 0,
			// Â≠òÂÇ®ÂéüÂßãÂ≠óÂÖ∏Êï∞ÊçÆ‰ª•‰æøÂêéÁª≠‰ΩøÁî®
			statusDict: customerStatus,
			approvalDict: customerProgress,
			filterParams: {
				department: '',
				branchId: '',
				status: '',
				approvalStatus: '',
				customerGroup: ''
			},
			page: 1,
			pageSize: 10,
			totalCount: 0,
			dictMaps: dictMaps,
			isLoading: false,
			hasMore: true
		}
	},
	onLoad() {
		
		// Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅ
		this.checkLogin();
		this.getDeptTree()

		// ÁõëÂê¨Âà∑Êñ∞ÂàóË°®‰∫ã‰ª∂
		uni.$on('refreshCustomerList', this.refreshCustomerList);
	},
	onShow() {
		// Âú®È°µÈù¢ÊòæÁ§∫Êó∂‰πüÊ£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅ
		if(this.checkLogin()) {
			this.loadCustomerList(true);
		}

		// Ê£ÄÊü•ÊùÉÈôê
		const permissions = uni.getStorageSync('permissions');
		if (!permissions || !permissions.customer) {
			uni.showToast({
				title: 'ÊÇ®Ê≤°ÊúâÊùÉÈôêËÆøÈóÆÂÆ¢Êà∑ÁÆ°ÁêÜ',
				icon: 'none',
				duration: 2000
			});
			
			// Âª∂ËøüÂêéË∑≥ËΩ¨Âà∞È¶ñÈ°µ
			setTimeout(() => {
				uni.switchTab({
					url: '/pages/dashboard/dashboard'
				});
			}, 1000);
			return;
		}
	},
	onReachBottom() {
		if (this.hasMore && !this.isLoading) {
			this.loadMore();
		}
	},
	onUnload() {
		// È°µÈù¢Âç∏ËΩΩÊó∂ÁßªÈô§‰∫ã‰ª∂ÁõëÂê¨
		uni.$off('refreshCustomerList', this.refreshCustomerList);
	},
	methods: {
		// Âà∑Êñ∞ÂÆ¢Êà∑ÂàóË°®ÔºàÁî®‰∫é‰∫ã‰ª∂ÁõëÂê¨Ôºâ
		refreshCustomerList() {
			this.loadCustomerList(true);
		},

		async getDeptTree() {
			try {
				const res = await department.getTreeList()
				console.log(res, 'res')

				if (res.success && res.retCode === 200 && res.data && res.data.length > 0) {
					// ÊèêÂèñÈÉ®Èó®Êï∞ÊçÆ
					const deptData = res.data[0].children || [];
					// ‰øùÂ≠òÂÆåÊï¥ÈÉ®Èó®Êï∞ÊçÆ‰ª•‰æõÂêéÁª≠‰ΩøÁî®
					this.departmentData = deptData;
					// ËÆæÁΩÆÈÉ®Èó®ÈÄâÈ°πÂàóË°®Ôºå"ÂÖ®ÈÉ®"Â∑≤Âú®ÂàùÂßãÂåñÊó∂Ê∑ªÂä†
					this.departmentOptions = ['ÂÖ®ÈÉ®', ...deptData.map(item => item.branch_name)];
				} else {
					this.departmentOptions = ['ÂÖ®ÈÉ®'];
					this.departmentData = [];
				}
			} catch (error) {
				console.error('Ëé∑ÂèñÈÉ®Èó®Êï∞ÊçÆÂ§±Ë¥•', error);
				this.departmentOptions = ['ÂÖ®ÈÉ®'];
				this.departmentData = [];
			}
		},

		// Âä†ËΩΩÊõ¥Â§öÊï∞ÊçÆ
		loadMore() {
			if (this.hasMore && !this.isLoading) {
				this.page++;
				this.loadCustomerList(false);
			}
		},

		// Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅ
		checkLogin() {
			const isLoggedIn = uni.getStorageSync('isLoggedIn');
			const token = uni.getStorageSync('token');
			const userInfo = uni.getStorageSync('userInfo');

			if (!isLoggedIn || !token || !userInfo) {
				console.log('customerÈ°µÈù¢Ê£ÄÊµãÂà∞Êú™ÁôªÂΩïÔºåÂáÜÂ§áË∑≥ËΩ¨Âà∞ÁôªÂΩïÈ°µ');

				uni.showToast({
					title: 'ËØ∑ÂÖàÁôªÂΩï',
					icon: 'none',
					duration: 2000
				});

				// Áõ¥Êé•Ë∑≥ËΩ¨Âà∞ÁôªÂΩïÈ°µ
				uni.redirectTo({
					url: '/pages/login/login'
				});

				return false;
			}
			return true;
		},
		loadCustomerList(isReset = true) {
			// Â¶ÇÊûúÊòØÈáçÁΩÆÔºåÂàôÊ∏ÖÁ©∫ÂàóË°®Âπ∂ÂõûÂà∞Á¨¨‰∏ÄÈ°µ
			if (isReset) {
				this.page = 1;
				this.customerList = [];
				this.hasMore = true;
			}

			// Â∑≤Êó†Êõ¥Â§öÊï∞ÊçÆÊó∂‰∏çÂÜçËØ∑Ê±Ç
			if (!this.hasMore) {
				return;
			}

			// ËÆæÁΩÆÂä†ËΩΩÁä∂ÊÄÅ
			this.isLoading = true;

			// ‰ªÖÂú®ÈáçÁΩÆÊó∂ÊòæÁ§∫Âä†ËΩΩÊèêÁ§∫
			if (isReset) {
				uni.showLoading({
					title: 'Âä†ËΩΩ‰∏≠...'
				});
			}

			// ÊûÑÂª∫ËØ∑Ê±ÇÂèÇÊï∞
			const params = {
				page: this.page,
				page_size: this.pageSize,
				phone: this.phone || '',
				name: this.searchKey || ''
			};

			// Â¶ÇÊûúÊúâÈÉ®Èó®Á≠õÈÄâÊù°‰ª∂ÔºåÊ∑ªÂä†Âà∞ËØ∑Ê±ÇÂèÇÊï∞‰∏≠
			if (this.filterParams.branchId) {
				params.branch_id = this.filterParams.branchId;
			} else {
				params.branch_id = '';
			}
			
			console.log(this.filterParams.status, 'this.filterParams.status')
			// Ê∑ªÂä†ÂÆ¢Êà∑Áä∂ÊÄÅÁ≠õÈÄâÊù°‰ª∂
			if (this.filterParams.status) {
				params.status = this.filterParams.status;
			} else {
				params.status = '';
			}
			
			// Ê∑ªÂä†ÂÆ°ÊâπÁä∂ÊÄÅÁ≠õÈÄâÊù°‰ª∂
			if (this.filterParams.approvalStatus) {
				params.deal_status = this.filterParams.approvalStatus;
			} else {
				params.deal_status = '';
			}

			// Ë∞ÉÁî®APIËé∑ÂèñÂÆ¢Êà∑ÂàóË°®
			customerApi.getList(params)
				.then(res => {
					// Âè™Âú®ÈáçÁΩÆÊó∂ÈöêËóèÂä†ËΩΩÊèêÁ§∫
					if (isReset) {
						uni.hideLoading();
					}

					if (res.success && res.retCode === 200 && res.data) {
						// Ëé∑ÂèñÊñ∞Êï∞ÊçÆ
						const newList = res.data.list || [];

						// Êõ¥Êñ∞ÊÄªÊï∞Èáè
						this.totalCount = res.data.total || 0;

						// ËøΩÂä†ÊàñÊõøÊç¢Êï∞ÊçÆ
						if (isReset) {
							this.customerList = newList;
						} else {
							this.customerList = [...this.customerList, ...newList];
						}

						// Âà§Êñ≠ÊòØÂê¶ËøòÊúâÊõ¥Â§öÊï∞ÊçÆ
						this.hasMore = newList.length >= this.pageSize && this.customerList.length < this.totalCount;

						// ÂàóË°®‰∏∫Á©∫Êó∂ÊòæÁ§∫ÊèêÁ§∫
						if (isReset && this.customerList.length === 0) {
							uni.showToast({
								title: 'ÊöÇÊó†ÂÆ¢Êà∑Êï∞ÊçÆ',
								icon: 'none'
							});
						}
					} else {
						// ÊòæÁ§∫ÈîôËØØ‰ø°ÊÅØ
						uni.showToast({
							title: res.message || 'Ëé∑ÂèñÂÆ¢Êà∑ÂàóË°®Â§±Ë¥•',
							icon: 'none'
						});
					}

					// ËÆæÁΩÆÂä†ËΩΩÁä∂ÊÄÅ‰∏∫false
					this.isLoading = false;
				})
				.catch(err => {
					// Âè™Âú®ÈáçÁΩÆÊó∂ÈöêËóèÂä†ËΩΩÊèêÁ§∫
					if (isReset) {
						uni.hideLoading();
					}

					// ÊòæÁ§∫ÈîôËØØ‰ø°ÊÅØ
					uni.showToast({
						title: 'Ëé∑ÂèñÂÆ¢Êà∑ÂàóË°®Â§±Ë¥•',
						icon: 'none'
					});

					console.error('Ëé∑ÂèñÂÆ¢Êà∑ÂàóË°®Â§±Ë¥•', err);

					// ËÆæÁΩÆÂä†ËΩΩÁä∂ÊÄÅ‰∏∫false
					this.isLoading = false;
				});
		},

			// ‰∏çÂÜçÈúÄË¶ÅÊò†Â∞ÑÔºåÁõ¥Êé•‰ΩøÁî®APIËøîÂõûÁöÑÈÉ®Èó®ID
		handleSearch() {
			this.loadCustomerList(true);
		},
		changeStatusFilter(status) {
			this.statusFilter = status;
			this.loadCustomerList(true);
		},
		formatDate(timestamp) {
			if (!timestamp) return '';
			const date = new Date(timestamp);
			return `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, '0')}-${String(date.getDate()).padStart(2, '0')}`;
		},
		getStatusText(status) {
			const map = {
				'1': 'Êñ∞ÂÆ¢Êà∑',
				'2': 'ÊÑèÂêëÂÆ¢Êà∑',
				'3': 'Â∑≤Êàê‰∫§',
				'4': 'Â∑≤ÊµÅÂ§±',
				'5': 'Â∑≤Áßª‰∫§',
				'new': 'Êñ∞ÂÆ¢Êà∑',
				'intention': 'ÊÑèÂêëÂÆ¢Êà∑',
				'deal': 'Â∑≤Êàê‰∫§',
				'lost': 'Â∑≤ÊµÅÂ§±'
			};
			return map[status] || status || 'Êú™Áü•';
		},
		getClientType(type) {
			const map = {
				1: 'Ê∂àË¥π',
				2: 'ÁªèËê•',
				3: 'Ê∂àË¥πÁªèËê•'
			};
			return map[type] || 'Êú™Áü•Á±ªÂûã';
		},
		getDealStatus(status) {
			const map = {
				'1': 'Êú™ÂÆ°Êâπ',
				'2': 'ÂÆ°Êâπ‰∏≠',
				'3': 'Â∑≤ÈÄöËøá',
				'4': 'Â∑≤ÊãíÁªù'
			};
			return map[status] || 'Êú™Áü•Áä∂ÊÄÅ';
		},
		getJinduText(jindu) {
			const map = {
				'1': 'ÂæÖÂÆ°Êâπ',
				'2': 'ÂÆ°Êâπ‰∏≠',
				'3': 'Â∑≤ÈÄöËøá',
				'4': 'Â∑≤ÊãíÁªù',
				'pending': 'ÂæÖÂÆ°Êâπ',
				'processing': 'ÂÆ°Êâπ‰∏≠',
				'approved': 'Â∑≤ÈÄöËøá',
				'rejected': 'Â∑≤ÊãíÁªù'
			};
			return map[jindu] || jindu || 'Êú™Áü•';
		},
		goDetail(item) {
			// Â∞ÜÂÆ¢Êà∑Êï∞ÊçÆÁºñÁ†Å‰∏∫URLÂèÇÊï∞
			const customerData = encodeURIComponent(JSON.stringify(item));
			uni.navigateTo({
				url: `/pages/customer/detail?id=${item.id}&customerData=${customerData}`
			});
		},
		goEdit(item) {
			// Â∞ÜÂÆ¢Êà∑Êï∞ÊçÆÁºñÁ†Å‰∏∫URLÂèÇÊï∞
			const customerData = encodeURIComponent(JSON.stringify(item));
			uni.navigateTo({
				url: `/pages/customer/add?id=${item.id}&customerData=${customerData}`
			});
		},
		goFollowup(item) {
			// Â∞ÜÂÆ¢Êà∑Êï∞ÊçÆÁºñÁ†Å‰∏∫URLÂèÇÊï∞
			const customerData = encodeURIComponent(JSON.stringify(item));
			uni.navigateTo({
				url: `/pages/followup/followup?id=${item.id}&customerData=${customerData}`
			});
		},
		updateStatus(item) {
			// Â∞ÜÂÆ¢Êà∑Êï∞ÊçÆÁºñÁ†Å‰∏∫URLÂèÇÊï∞
			const customerData = encodeURIComponent(JSON.stringify(item));
			console.log('updateStatus')
			uni.navigateTo({
				url: `/pages/customer/status?id=${item.id}&customerData=${customerData}`
			});
		},
		transferCustomer(item) {
			const customerData = encodeURIComponent(JSON.stringify(item));
			uni.navigateTo({
				url: `/pages/customer/transfer?id=${item.id}&customerData=${customerData}`
			});
		},
		createLoan(item) {
			uni.navigateTo({
				url: `/pages/loan/create?customerId=${item.id}`
			});
		},
		goAdd() {
			uni.navigateTo({
				url: `/pages/customer/add`
			});
		},
		toggleFilterForm() {
			this.showFilterForm = !this.showFilterForm;
		},
		departmentChange(e) {
			this.currentDepartment = e.detail.value;
			
			if (this.currentDepartment === 0) {
				// ÈÄâÊã©"ÂÖ®ÈÉ®"Êó∂Ôºå‰∏çËÆæÁΩÆÈÉ®Èó®Á≠õÈÄâ
				this.filterParams.department = '';
				this.filterParams.branchId = '';
			} else {
				// ÈÄâÊã©ÂÖ∑‰ΩìÈÉ®Èó®Êó∂ÔºåËé∑ÂèñÈÉ®Èó®‰ø°ÊÅØ
				const selectedIndex = this.currentDepartment - 1; // Âõ†‰∏∫Á¨¨‰∏Ä‰∏™ÊòØ"ÂÖ®ÈÉ®"
				if (selectedIndex >= 0 && selectedIndex < this.departmentData.length) {
					const dept = this.departmentData[selectedIndex];
					this.filterParams.department = dept.branch_name;
					this.filterParams.branchId = dept.id;
				}
			}
		},
		statusChange(e) {
			this.currentStatus = e.detail.value;
			if (e.detail.value == 0) {
				// ÈÄâÊã©"ÂÖ®ÈÉ®"Êó∂Ôºå‰∏çËÆæÁΩÆÁä∂ÊÄÅÁ≠õÈÄâ
				this.filterParams.status = '';
			} else {
				// ÈÄâÊã©ÂÖ∑‰ΩìÁä∂ÊÄÅÊó∂Ôºå‰ΩøÁî®ÂØπÂ∫îÁöÑvalueÂÄº
				const selectedIndex = e.detail.value - 1; // Âõ†‰∏∫Á¨¨‰∏Ä‰∏™ÊòØ"ÂÖ®ÈÉ®"
				if (selectedIndex >= 0 && selectedIndex < this.statusDict.length) {
					this.filterParams.status = this.statusDict[selectedIndex].value;
				}
			}
		},
		approvalChange(e) {
			this.currentApproval = e.detail.value;
			if (e.detail.value == 0) {
				// ÈÄâÊã©"ÂÖ®ÈÉ®"Êó∂Ôºå‰∏çËÆæÁΩÆÂÆ°ÊâπÁä∂ÊÄÅÁ≠õÈÄâ
				this.filterParams.approvalStatus = '';
			} else {
				// ÈÄâÊã©ÂÖ∑‰ΩìÂÆ°ÊâπÁä∂ÊÄÅÊó∂Ôºå‰ΩøÁî®ÂØπÂ∫îÁöÑvalueÂÄº
				const selectedIndex = e.detail.value - 1; // Âõ†‰∏∫Á¨¨‰∏Ä‰∏™ÊòØ"ÂÖ®ÈÉ®"
				if (selectedIndex >= 0 && selectedIndex < this.approvalDict.length) {
					this.filterParams.approvalStatus = this.approvalDict[selectedIndex].value;
				}
			}
		},
		resetFilters() {
			this.currentDepartment = 0;
			this.currentStatus = 0;
			this.currentApproval = 0;
			this.currentCustomerGroup = 0;
			this.filterParams = {
				department: '',
				branchId: '',
				status: '',
				approvalStatus: '',
				customerGroup: ''
			};
			uni.showToast({
				title: 'Á≠õÈÄâÂ∑≤ÈáçÁΩÆ',
				icon: 'none'
			});
		},
		customerGroupChange(e) {
			this.currentCustomerGroup = e.detail.value;
			this.filterParams.customerGroup = this.currentCustomerGroup === 0 ? '' : this.currentCustomerGroup;
		},
		applyFilters() {
			this.loadCustomerList(true);
			uni.showToast({
				title: 'Á≠õÈÄâÊù°‰ª∂Â∑≤Â∫îÁî®',
				icon: 'none'
			});
			this.showFilterForm = false;
		}
	}
}
</script>

